import { NgModule }                  from '@angular/core';
import { Routes, RouterModule }      from '@angular/router';
import { CommonModule }       from '@angular/common';
import { FormsModule, ReactiveFormsModule }        from '@angular/forms';

import { SharedModule } from '../../shared/shared.module';
import { ImportExportComponent } from './import-export.component';
import { ImportServicesComponent } from './import-services.component';
import { ImportProductsComponent } from './import-products.component';






const routes: Routes = [
    { path: 'import-customers', component: ImportExportComponent },
    { path: 'import-services', component: ImportServicesComponent },
    { path: 'import-products', component: ImportProductsComponent },
];

@NgModule({
  imports: [  CommonModule, FormsModule, ReactiveFormsModule, SharedModule, RouterModule.forChild(routes) ],
  declarations: [
    ImportExportComponent,
    ImportServicesComponent,
    ImportProductsComponent
  ],
  exports : [CommonModule]
})

export class ImportExportModule { }



