import { Component, OnInit } from "@angular/core";
import { RestService } from "../../common/rest.service";
import { Storage } from "../../shared/utils/storage";
import {
  MdDialog,
  MdSort,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";
import { Prop } from "../../common/props";

@Component({
  selector: "app-import-export",
  templateUrl: "./import-export.component.html",
  styleUrls: ["./import-export.component.scss"]
})
export class ImportExportComponent implements OnInit {
  url: any;
  myFile: any;
  companyId: any;
  data: any = [];
  failureRecordsNo: number = 0;
  duplicateRecordsNo: number = 0;
  successfulRecordsNo: number = 0;
  totalRecords: number = 0;
  failureRecords: any = [];
  duplicateRecords: any = [];
  successfulRecords: any = [];

  constructor(private restService: RestService) {}

  ngOnInit() {
    this.url = Prop.REPORT_URL + "import/customer";

    this.companyId = Storage.getSessionUser().company.id;
  }
  viewFile(event) {
    let fileList: FileList = event.target.files;

    let file: File = fileList[0];
    this.myFile = file;
    let _formData: FormData = new FormData();
    _formData.append("file", this.myFile);
    _formData.append("companyId", this.companyId);
    this.restService.customerImport(_formData).then((data: any) => {
      this.data = data.SuccessfulRecords.data;
      this.failureRecordsNo = data.FailedRecords.total;
      this.successfulRecordsNo = data.SuccessfulRecords.total;
      this.duplicateRecordsNo = data.DuplicateRecords.total;
      this.failureRecords = data.FailedRecords.data;
      this.successfulRecords = data.SuccessfulRecords.data;
      this.duplicateRecords = data.DuplicateRecords.data;
      this.totalRecords = data.totalRecords;
      this.myFile = [];
      fileList = null;
      file = null;
    });
  }
  upload() {}
  // download() {
  //   let data = {

  //   }
  //   this.restService.customerExport(data, 'report', '.xlsx')

  // }
  tabClick($event) {
    console.log($event);
  }
  clickDom($dom) {
    $dom.value = "";
  }
}



